<template>
    <div class="right_sliding" :class="{close: !open}">
        <div class="open_btn" @click="openOrClose">
            <img src="@/assets/icon_right1.png" class="img" :class="{close: !open}" alt="">
            <!-- <div class="before"></div>
            <div class="after"></div> -->
        </div>
        <div class="tab">
            <span class="item" @click="changeTap()">列表视图</span>
            <span class="item active">地图视图</span>
        </div>
        <div class="line2"></div>
        <slot></slot>
    </div>
</template>

<script>
export default {
    props: {
        width: {
            type: Number,
            default: 328
        }
    },
    data(){
        return {
            open: false
        }
    },
    methods: {
        openOrClose() {
            this.open = !this.open
        },
        changeTap() {
            this.$router.push(this.$route.name + 'list')
        }
    },
}
</script>

<style lang="less" scoped>
.right_sliding {
    box-sizing: border-box;
    position: fixed;
    right: 0;
    width: 328px;
    height: 100%;
    background-color: #454d65;
    padding: 40px 24px;
    transition: all 0.5s;
    transform: translateX(0);
    &.close {
        transform: translateX(100%);
    }
    .open_btn {
        position: absolute;
        cursor: pointer;
        left: -20px;
        top: 50%;
        transform: translateY(-50%);
        width: 20px;
        height: 100px;
        background: #454d65;
        overflow: hidden;
        .img {
            position: absolute;
            left: 50%;
            top: 50%;
            transition: all 0.2s;
            transform: translate(-50%, -50%);
            &.close {
                transform: translate(-50%, -50%) rotate(-180deg);
            }
        }
        .before {
            position: absolute;
            top: -14px;
            left: -14px;
            width: 28.5px;
            height: 28.5px;
            background-color: #212633;
            transform: rotate(-45deg)
        }
        .after {
            position: absolute;
            bottom: -14px;
            left: -14px;
            width: 28.5px;
            height: 28.5px;
            background-color: #212633;
            transform: rotate(-45deg)
        }
    }
    .line2 {
        width: 100%;
        height: 1px;
        margin-top: 20px;
        background-image: url(../assets/Line2.png);
    }
}
</style>
